import { debounce } from "radash";
export default {
  data() {
    return {
      debouncers: {},
      triggers: {}
    };
  },
  beforeDestroy() {
    this.removeDebounce();
  },
  methods: {
    initDebounce(key, params) {
      const trigger = this.triggers[key]?.trigger;
      if (!trigger) return;
      if (this.debouncers[key]) this.debouncers[key].cancel();
      else {
        const delay =
          this.config?.debounceTimer || this.triggers?.[key]?.timer || 500;
        this.debouncers[key] = debounce({ delay }, () => {
          this[trigger](params);
        });
      }
      this.debouncers[key]();
    },
    removeDebounce(keys = null) {
      if (keys && !Array.isArray(keys)) keys = [keys];
      (keys || Object.keys(this.debouncers)).forEach((key) => {
        this.debouncers[key]?.cancel?.();
        this.debouncers[key] = null;
      });
    }
  }
};
